<template>
    <div>
        <Row>
            <label class="margin-left-10" style="margin-right:8px;">产品名称：</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px"/>
            <label class="margin-left-10" style="margin-right:8px;">票据名称：</label>
            <Input v-model="table.query.subName" placeholder="" clearable style="width: 200px"/>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <div v-if="!haveData" class="no-data">当前没有可预订的产品</div>
                <template v-else>
                    <Card class="product-item" v-for="item in table.data" :key="item.id">
                        <div class="product-item-header" slot="title">
                            <span class="product-item-header-title">{{item.name}}</span>
                            <Tag color="blue">{{item.epName}}</Tag>
                        </div>
                        <Table :columns="sub.col" :data="item.subs"></Table>
                    </Card>
                    <div style="margin: 10px;overflow: hidden">
                        <div style="float: right;">
                            <Page :total="table.total" :show-sizer="true" placement="top"
                                  @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                        </div>
                    </div>
                </template>
            </i-col>
        </Row>
        <Modal v-model="showCalendar" draggable footer-hide title="选择日期" width="680" :mask-closable="false">
            <vue-cal ref="cal"
                     style="height: 500px"
                     class="vuecal--green-theme price-cal"
                     locale="zh-cn"
                     hide-view-selector
                     events-on-month-view
                     :disable-views="['years', 'year', 'week', 'day']"
                     :dbl-click-to-navigate="false"
                     default-view="month"
                     :events="events"
                     @view-change="loadPriceCalendar"
                     @event-focus="chooseDay"/>
        </Modal>
        <Modal v-model="showBuy" :mask-closable="false" @on-ok="addOrUpdate" @on-cancel="cancel" :loading="loadingBtn" ok-text="提交订单" title="确认订单" width="860">
            <div style="height: 600px">
                <vue-scroll ref="vScroll">
                    <Form ref="form" v-if="plan" :model="order" :rules="validate" class="order-form">
                        <div>
                            <b class="order-title">{{currentProduct.name}} - {{currentSub.name}}</b>
                        </div>
                        <Divider/>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                游玩日期:
                            </div>
                            {{plan.booking}}
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                有效期:
                            </div>
                            {{plan.effective}}
                            至
                            {{plan.expiry}}
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                购买后多少分钟才能使用:
                            </div>
                            {{plan.useSecondLimit / 60}}分钟
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                禁用星期:
                            </div>
                            {{plan.disabledWeeks.join(',')}}
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                禁用日期:
                            </div>
                            <div v-if="plan.disableDate">{{plan.disableDate}}</div>
                            <Tag v-else type="success">无使用日期限制</Tag>
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                时间段:
                            </div>
                            <Select v-model="order.items[0].booking">
                                <Option v-for="time in plan.times" :value="time.startTime" :key="time.start">{{time.start}} ~ {{time.end}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                最低售价:
                            </div>
                            <div class="price">
                                ￥{{ForceMoney(plan.lowPrice)}}元
                            </div>
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                结算价:
                            </div>
                            <div class="price">
                                ￥{{ForceMoney(plan.settlePrice)}}元
                            </div>
                        </FormItem>
                        <FormItem class="order-form-item">
                            <div class="order-form-label" slot="label">
                                当前库存:
                            </div>
                            已售 {{currentTime.sold}} / 剩余 {{currentTime.stock}}
                        </FormItem>
                        <Divider orientation="left">
                            联系人信息
                        </Divider>
                        <FormItem label="姓名:" :label-width="120" prop="shipping.name">
                            <Input v-model.trim="order.shipping.name"/>
                        </FormItem>
                        <FormItem label="手机号:" :label-width="120" prop="shipping.phone">
                            <Input v-model.trim="order.shipping.phone"/>
                        </FormItem>
                        <FormItem label="性别:" :label-width="120" prop="shipping.gender">
                            <RadioGroup v-model="order.shipping.gender">
                                <Radio :label="item.id" v-for="item in MemberSexType.filter(i => i.id !== 0)" :key="item.id">{{item.name}}</Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem label="身份证:" :label-width="120" prop="shipping.cardNo">
                            <Input v-model.trim="order.shipping.cardNo"/>
                        </FormItem>
                        <Divider ref="visitorPoint" orientation="left">游客信息</Divider>
                        <div class="order-form-visitor">
                            <Row class="order-form-visitor-header">
                                <i-col span="5" class="required">手机号</i-col>
                                <i-col span="4" class="required">姓名</i-col>
                                <i-col span="4" :class="{ required: currentSub.requireSid }">证件类型</i-col>
                                <i-col span="5" :class="{ required: currentSub.requireSid }">证件号</i-col>
                                <i-col span="3" class="required" style="flex: 1">票数</i-col>
                                <i-col span="3"></i-col>
                            </Row>
                            <Row v-for="(item, index) in order.items[0].visitors" :key="index">
                                <i-col span="5">
                                    <FormItem :prop="`items.0.visitors.${index}.phone`" :rules="validate.visitorPhone" class="order-form-visitor-item">
                                        <Input v-model.trim="item.phone" />
                                    </FormItem>
                                </i-col>
                                <i-col span="4">
                                    <FormItem :prop="`items.0.visitors.${index}.name`" :rules="validate.visitorName" class="order-form-visitor-item">
                                        <Input v-model.trim="item.name" />
                                    </FormItem>
                                </i-col>
                                <i-col span="4">
                                    <FormItem :prop="`items[0].visitors[${index}].cardType`" class="order-form-visitor-item">
                                        <RadioGroup v-model="item.cardType">
                                            <Radio :label="item.id" v-for="item in CardType" :key="item.id">{{item.name}}</Radio>
                                        </RadioGroup>
                                    </FormItem>
                                </i-col>
                                <i-col span="5">
                                    <FormItem :prop="`items[0].visitors[${index}].cardNo`" :rules="validate.visitorCardNo" class="order-form-visitor-item">
                                        <Input v-model="item.cardNo"/>
                                    </FormItem>
                                </i-col>
                                <i-col span="3">
                                    <FormItem :prop="`items[0].visitors[${index}].quantity`" class="order-form-visitor-item" style="flex: 1">
                                        <InputNumber v-model="item.quantity" :min="1"/>
                                    </FormItem>
                                </i-col>
                                <i-col span="3">
                                    <FormItem style="width: 95px">
                                        <Button v-if="index > 0" type="dashed" icon="md-remove" @click="removeVisitor(index)"/>
                                        <Button v-if="index === 0" type="dashed" @click="copyToVisitor">复制联系人</Button>
                                    </FormItem>
                                </i-col>
                            </Row>
                            <Button type="dashed" long icon="md-add" @click="addVisitor">新增游客</Button>
                        </div>
                        <Divider orientation="left">订单备注</Divider>
                        <FormItem prop="remark">
                            <Input type="textarea" v-model.trim="order.remark" :maxlength="150"/>
                        </FormItem>
                    </Form>
                    <Row type="flex" justify="end" align="middle">
                        <div>
                            订单金额：
                        </div>
                        <h1 style="color: coral;">￥{{amount}}</h1>
                    </Row>
                </vue-scroll>
            </div>
        </Modal>
        <Pay v-model="showPay" :order="submitResult.order" :shipping="submitResult.shipping" :items="submitResult.items"/>
    </div>
</template>

<script>
import dayjs from 'dayjs';
import get from 'lodash.get';
import VueCal from 'vue-cal';
import 'vue-cal/dist/vuecal.css';
import { DateFormat, ForceMoney, calcEffective, extractTimes, transformWeeks, ValidateIdCard } from '@/libs/common';
import { Weeks, MemberSexType, CardType, MobileRegexp } from '@/libs/dic';
import ModelView from '@/mixins/model-view';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import Pay from '@/fx/views/pay';

export default {
    name: 'BookingScenic',
    components: { Pay, VueCal },
    mixins: [TableDataView, TableColRender, ModelView],
    data () {
        return {
            Weeks,
            CardType,
            MemberSexType,
            ForceMoney,
            DateFormat,
            showCalendar: false,
            showBuy: false,
            showPay: false,
            events: [],
            sub: {
                col: [{
                    title: '票据名称',
                    key: 'name'
                }, {
                    title: '过期时间',
                    key: 'expiryMemo'
                }, {
                    title: '市场价',
                    key: 'marketPrice',
                    render: this.renderMoney
                }, {
                    title: '库存(售/余)',
                    align: 'center',
                    render: (h, params) => this.renderJoin(h, params)('saleQuantity', 'stock')
                }, {
                    title: '退货规则',
                    key: 'refundName',
                    tooltip: true
                }, {
                    title: '操作',
                    key: 'action',
                    width: 200,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: {
                            loading: this.loadingBtn,
                            type: 'warning',
                            shape: 'circle',
                            disabled: this.showCalendar,
                            style: { marginRight: '5px' }
                        },
                        click: () => {
                            this.currentSub = params.row;
                            this.currentProduct = this.table.data.find(p => p.id === this.currentSub.productId);
                            this.showCalendar = true;
                            this.$refs.cal.switchView('month');
                        },
                        text: '订购'
                    }])
                }]
            },
            table: {
                query: {
                    name: null,
                    subName: null
                },
                url: '/api/product/plan/sale/list'
            },
            plan: null,
            calendar: null,
            currentSub: null,
            currentProduct: null,
            order: {
                shipping: {
                    name: null,
                    phone: null,
                    gender: 1,
                    cardType: 1,
                    cardNo: null
                },
                remark: null,
                items: [{
                    booking: null,
                    productSubNumber: null,
                    visitors: [{
                        name: null,
                        phone: null,
                        quantity: 1,
                        cardType: 1,
                        cardNo: null,
                        gender: null
                    }]
                }]
            },
            addUrl: '/api/fx/order/create/scenic',
            validate: {
                'shipping.name': [{ required: true, trigger: 'blur', message: '请输入联系人姓名' }],
                'shipping.phone': [{ required: true, trigger: 'blur', pattern: MobileRegexp, message: '请输入联系人手机号' }],
                'shipping.cardNo': [{ trigger: 'blur',
                    validator: (rule, value, callback) => {
                        if (!value) {
                            callback();
                            return;
                        }
                        if (ValidateIdCard(value)) {
                            callback();
                            return;
                        }
                        callback(new Error('请输入正确的身份证号码'));
                    }
                }],
                visitorName: [{ required: true, trigger: 'blur', message: '请输入游客姓名' }],
                visitorPhone: [{ required: true, trigger: 'blur', pattern: MobileRegexp, message: '请输入游客手机号' }],
                visitorCardNo: [{ trigger: 'blur', validator: this.validateVisitorCard }]
            },
            submitResult: {
                order: null,
                shipping: null,
                items: null
            }
        };
    },
    computed: {
        haveData () {
            return !!this.table.data?.length;
        },
        currentTime () {
            if (!this.plan || !this.order.items[0].booking) {
                return null;
            }
            return this.plan.times.find(time => time.startTime === this.order.items[0].booking);
        },
        amount () {
            if (!this.plan) {
                return 0;
            }
            const quantity = this.order.items[0].visitors.reduce((total, v) => total + v.quantity, 0);
            return ForceMoney(this.plan.settlePrice * quantity);
        },
        generateAddOrUpdate () {
            return {
                ...this.order
            };
        }
    },
    methods: {
        processQueryData (data) {
            return data.map(item => ({
                ...item,
                subs: item.subs.map(sub => ({
                    ...sub,
                    expiryMemo: this.parseExpiryMemo(sub)
                }))
            }));
        },
        parseExpiryMemo (sub) {
            return sub.effectiveType === 1
                ? sub.effectiveDay === 1 ? '当天有效' : `预定后${sub.effectiveDay - 1}天`
                : `至${DateFormat(sub.effectiveEndDate, 'YYYY-MM-DD')}`;
        },
        async loadPriceCalendar ({ startDate }) {
            this.plan = null;
            const result = await this.get('api/product/plan/sale/calendar', {
                productSubId: this.currentSub.id,
                beginTime: DateFormat(startDate),
                endTime: DateFormat(dayjs(dayjs(startDate).endOf('month')).endOf('day').valueOf())
            });
            this.calendar = result.success ? result.value : {};
            this.events = this.$refs.cal.viewCells.map(cell => {
                const list = this.calendar[cell.formattedDate];
                const price = list && list.sort((a, b) => a.start > b.start ? 1 : a.start === b.start ? 0 : -1)[0];
                let content;
                if (price) {
                    content = `<div class="price-cal-cell-event-info">
                            <div>
                                <label>市场价:</label><span>￥${ForceMoney(price.marketPrice)}</span>
                            </div>
                            <div>
                                <label>结算价:</label><span>￥${ForceMoney(price.settlePrice)}</span>
                            </div>
                            <div>
                                <label>库存:</label><span>${price.sold}/${price.stock}</span>
                            </div>
                        </div>`;
                } else {
                    content = '暂无计划';
                }
                return {
                    start: cell.formattedDate,
                    end: cell.formattedDate,
                    class: price ? 'price-cal-cell-event-have' : 'price-cal-cell-event-no',
                    content
                };
            });
        },
        chooseDay ({ start, startDate }) {
            const now = dayjs();
            if (dayjs(startDate).isBefore(now.startOf('day'))) {
                this.$Notice.warning({ title: '当前日期已过期' });
                return;
            }
            const plans = this.calendar[start];
            if (plans) {
                const plan = plans[0];
                const { useSecondLimit, marketPrice, settlePrice, lowPrice, refundRule, disableWeek, disableDate } = plan;
                const currentPlan = {
                    booking: plan.day,
                    ...calcEffective(plan),
                    useSecondLimit,
                    marketPrice,
                    settlePrice,
                    lowPrice,
                    refundRule,
                    disabledWeeks: transformWeeks(disableWeek),
                    disableDate,
                    times: extractTimes(plans)
                };
                const valueOf = now.valueOf();
                const time = currentPlan.times
                    .sort((a, b) => a.startTime > b.startTime ? 1 : a.startTime === b.startTime ? 0 : -1)
                    .filter(time => time.endTime > valueOf);
                if (!time?.length) {
                    this.$Notice.warning({ title: '当前时间段无计划' });
                    return;
                }
                this.plan = currentPlan;
                this.order.items[0].booking = time?.[0].startTime || this.plan.times[0].startTime;
                this.order.items[0].productSubNumber = this.currentSub.code;
            }
            this.showCalendar = !plans;
            this.showBuy = !!plans;
            if (this.showBuy) {
                this.vScrollToTop();
                this.loadingBtn = true;
                this.$nextTick(() => this.$refs[this.formRef].resetFields());
            }
        },
        addVisitor () {
            this.order.items[0].visitors.push({
                name: null,
                phone: null,
                quantity: 1,
                cardType: 1,
                cardNo: null,
                gender: null
            });
        },
        removeVisitor (index) {
            this.order.items[0].visitors.splice(index, 1);
        },
        copyToVisitor () {
            const visitor = this.order.items[0].visitors[0];
            const newer = {
                ...this.order.shipping,
                quantity: 1
            };
            if (!visitor.name && !visitor.phone && !visitor.cardNo) {
                this.order.items[0].visitors.splice(0, 1, newer);
                return;
            }
            this.order.items[0].visitors.unshift(newer);
        },
        validateVisitorCard (rule, value, callback) {
            const { requireSid } = this.currentSub;
            const visitor = get(this.order, rule.field.substring(0, rule.field.lastIndexOf('.')));
            if (!value && !requireSid) {
                callback();
                return;
            }
            if (requireSid && !value) {
                callback(new Error('请输入证件号'));
                return;
            }
            if (visitor.cardType === 1 && value) {
                if (!ValidateIdCard(value)) {
                    callback(new Error('请输入正确的身份证号码'));
                    return;
                }
            }
            callback();
        },
        afterAddOrUpdate (success, result) {
            if (success) {
                this.showBuy = false;
                this.submitResult.order = result.value;
                this.submitResult.shipping = result.data.shipping;
                this.submitResult.items = result.data.items;
                this.showPay = true;
            }
        }
    }
};
</script>
<style lang="less">
    .price-cal {
        .vuecal__event {
            background: none;
        }

        .vuecal__event--focus {
            box-shadow: none;
        }

        .vuecal__cell-date {
            font-size: 14px;
        }

        &-cell-event-no {
            color: #c5c8ce;
        }

        &-cell-event-info {
            display: flex;
            flex-direction: column;
            padding: 0 5px;
            span {
                color: red;
            }
        }
    }
</style>
<style scoped>
    .product-item >>> .ivu-card-body {
        padding: 0;
    }

    .product-item >>> .ivu-table td {
        border-bottom: 0;
    }

    .product-item >>> .ivu-card-head {
        padding: 10px 16px;
    }

    .product-item >>> .ivu-table-wrapper {
        border: 0;
    }

    .product-item >>> .ivu-table:before {
        background: none;
    }

    .product-item >>> .ivu-table:after {
        background: none;
    }

    .order-form-visitor >>> .ivu-form-item-label {
        float: none;
    }
</style>
<style lang="less" scoped>
    .no-data {
        display: flex;
        justify-content: center;
    }

    .product {
        &-item {
            margin-bottom: 15px;
            &-header {
                display: flex;
                align-items: center;

                &-title {
                    font-size: 25px;
                    font-weight: bold;
                    margin-right: 10px;
                }
            }
        }
    }

    .order-title {
        font-size: 25px;
    }
    .price {
        color: red;
        font-size: 14px;
    }
    .order-form {
        &-item {
            display: flex;
            align-items: center;
            margin-bottom: 0;
        }
        &-label {
            font-weight: bold;
            font-size: 15px;
            width: 170px;
        }
        &-visitor {
            &-header {
                font-weight: bold;
                font-size: 14px;
                div {
                    flex: 2;
                }
            }
            &-item {
                flex: 2;
                margin-right: 10px;
            }
        }
    }
</style>
